.main__test {
  padding: 50px 150px;
  font-family: 'Lexend';
  position: relative;
  // background-color: red;
 
}
.main__test__header{
  padding: 20px 100px;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  background: #ffff ;
}
.main__test__title {
  max-width: 1620px;
  margin: 0 auto;
  font-family: 'Lexend';
  text-align: center;
  padding: 5px 0;
  background: rgba(0, 24, 244, 0.2);
  font-style: normal;
  font-weight: 400;
  font-size: 40px;
  color: #000000;

}

.main__test__time {
  max-width: 1620px;
  margin: 0 auto;
  margin-top: 30px;
  display: block;
  text-align: center;
  font-family: 'Lexend';
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 40px;
  color: #5E12DA;
}

.test__step {
  max-width: 1620px;
  margin: 0 auto;
  display: flex;
  align-items: center;

  h4 {
    font-style: normal;
    font-weight: 400;
    font-size: 25px;
    color: #000000;
  }
}

.test__step__list {

  margin-left: 30px;
  
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-grow: 1;
}

.test__step__item {
  border: 1px solid #5E12DA;
  border-radius: 50%;
  padding: 6px 12px;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  color: #5E12DA;
}

.test__step__active {
  border: 1px solid #5E12DA;
  border-radius: 50%;
  padding: 6px 12px;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  color: #5E12DA;
  background: rgba(0, 24, 244, 0.2);
}

.test__list {
  max-width: 1275px;
  margin: 0 auto;
  margin-top: 300px;
  list-style-type: none;
}

.test__item {
  margin-top: 50px;
  padding:  20px 0;
  background: #FFFFFF;
  box-shadow: 0px 4px 20px rgba(11, 167, 191, 0.2);
  .test__item__inner{
    img{
      width: 340px !important;
      height: 280px !important;
      margin: 20px  20px 20px 0;
      object-fit: cover;
    }
  }
}


.test__item__title {
  display: flex;
  align-items: center;
  padding-left: 20px;
  font-style: normal;
font-weight: 300;
font-size: 24px;
line-height: 30px;
  strong{
    font-size: 20px;
    font-weight: 600;
  }
  img{
    width: 110px !important;
    height: 110px !important;
    object-fit: cover;
    margin: 0 20px !important;

  }

}

.test__item__inner {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  
  span {
    margin-top: 15px;
    padding: 4px 0;
  }
}

.test__finsh__btn{
  background: #5E12DA;
  box-shadow: 0px 10px 20px rgba(11, 167, 191, 0.2);
  border-radius: 10px;
  width: 455px ;
  padding: 25px 0;
  margin: 0 auto;
  margin-top: 100px;
  text-align: center;
  display: block;
  color: white;
  border: none;
}

.test__item__variant{
  
  font-style: normal;
font-weight: 300;
font-size: 24px;
  margin-top: 10px;
  width: 100%;
  height: 40px;
  padding: 0 20px !important;
  label{
    display: flex;
    align-items: center;
    height: 100%;
    cursor: pointer;
    width: 100% !important;
  }
  input{
    display: none;
  }
}
.item__variant__active{
  background: rgba(0, 24, 244, 0.2);
}

.end__time{
 
  
  display: flex;
  align-items: center;
  justify-self: center;
  color: #5E12DA;
  background-color: #fff;
  padding: 10px 25px;
  margin: 0 auto;
  margin-top: 10px;
  border: none;
  font-size: 30px;
  animation: borderWarning 0.8s infinite linear both;
  border: 2px solid #5E12DA;
  border-radius: 10px;
  cursor: pointer;
  &:hover{
    background-color: #fff !important;
    color: #5E12DA !important;
  }
  }

  @keyframes borderWarning {
    50%{
      background: #5E12DA;
      color: white;
    }
    100%{
      background: white;
      color: #5E12DA;;
    }
  }